// const contractAddress = '.....'
import web3js  from 'web3'
// 智能合约ABI
const contractABI=[]
var contract=null;
/**
 * 初始化
 * @param {Object} callback 返回账户地址
 */
function Init(callback){
//判断用户是否安装MetaMask钱包插件
  if (typeof window.ethereum === "undefined") {
    //没安装MetaMask钱包进行弹框提示
    // alert("您需要一个Dapp浏览器才可以使用，请安装MetaMask");
  } else {
    //如果用户安装了MetaMask，可以要求他们授权应用登录并获取其账号
    ethereum.enable()
    .catch(function (reason) {
        //如果用户拒绝了登录请求
      if (reason === "User rejected provider access") {
        // 用户拒绝登录后执行语句；
        alert("您当前拒绝了授权登录")
      } else {
        alert("请授权MetaMask连接")
      }
      }).then(
        function (accounts) 
        {
          //如果用户同意了登录请求，可以拿到用户的账号
          //初始化provider
          let provider = window['ethereum'] || window.ethereum
          //初始化Web3
          window.web3 = new web3js(provider)
          // console.log("----------------",window.web3,"--------------")
          window.web3.eth.net.getId().
          then(
            function (result) {
            let currentChainId = result
            // console.log(currentChainId)
            //设置最大监听器数量，否则出现warning
            window.ethereum.setMaxListeners(300)
            // 从json获取到当前网络id下的合约地址
            // if(ContractAddress !== undefined){
            //   //实例化合约
              // window.MyContract = new window.web3.eth.Contract(cardAbi.abi,ContractAddress)
            //   //获取到当前默认的以太坊地址
            window.defaultAccount = accounts[0].toLowerCase()
            callback(window.defaultAccount)
          })
      }
      );
  }  
}
export default {
  Init
}